<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="create-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="发现隐患" left-arrow @click-left="onClickLeft"></van-nav-bar>

    <van-cell-group>
      <van-cell required title="排查日期：" is-link clickable @click="handleSelectExameDate">
        <span>{{ form.exameDate || '请选择'}}</span>
      </van-cell>
      <van-cell required title="检查部门：" is-link clickable @click="handleSelectDepartment">
        <span>{{ form.exameDepartment && form.exameDepartment.name || '请选择'}}</span>
      </van-cell>
      <van-cell required title="检查人：" is-link clickable @click="handleSelectUser">
        <!-- <span>{{ form.exameDepartment && form.exameDepartment.name }}</span> -->
        <span>{{form.exameUser.map(item => item.name).join('、') || '请选择'}}</span>
      </van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="所属专业" is-link clickable @click="handleSelectProfession">
        <span>{{ form.profession && form.profession.name }}</span>
      </van-cell>
      <van-cell title="故障设备分类" is-link clickable @click="handleSelectDeviceCategory">
        <span>{{ form.deviceCategory && form.deviceCategory.name }}</span>
      </van-cell>
      <van-cell class="detail-title" title="具体位置："></van-cell>
      <van-field v-model="form.address" placeholder="请填写" type="textarea" :rows="1" autosize />

      <van-cell title="隐患级别：" is-link clickable @click="handleSelectLevel">
        <span>{{ form.troubleLevel && form.troubleLevel.name || '请选择'}}</span>
      </van-cell>
      <van-cell required title="隐患类型：" is-link clickable @click="handleSelectType">
        <span>{{ form.troubleType && form.troubleType.name || '请选择'}}</span>
      </van-cell>
      <van-cell required class="detail-title" title="隐患内容："></van-cell>
      <van-field v-model="form.troubleContent" placeholder="请填写隐患内容" type="textarea" :rows="2" autosize />

      <van-cell class="detail-title" title="危害及影响："></van-cell>
      <van-field v-model="form.troubleSuggest" placeholder="请填写" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="临时解决措施："></van-cell>
      <van-field v-model="form.tempSolution" placeholder="请填写" type="textarea" :rows="2" autosize />

      <van-cell class="detail-title" title="最终解决措施："></van-cell>
      <van-field v-model="form.finalSolution" placeholder="请填写" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell required title="所属工区（站）：" is-link clickable @click="handleSelectDepartment2">
        <span>{{ form.dutyDepartment && form.dutyDepartment.name || '请选择'}}</span>
      </van-cell>
      <van-cell title="工区：" is-link clickable @click="handleSelectArea">
        <span>{{ form.dutyArea && form.dutyArea.name || '请选择'}}</span>
      </van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="备注："></van-cell>
      <van-field v-model="form.remark" placeholder="请填写" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="现场情况："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in form.imageList" :key="index" @click="handlePlayImages(index)">
          <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
          <auto-image
            :src="item.url"
            :width="108"
            :height="108"
            style="margin-bottom: 12px;margin-left: 12px;"
          ></auto-image>
          <div class="image-remove" @click.stop="handleImageRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;" @click.stop="handlePhoto1">
          <img src="../../assets/images/image-add.png" />
        </div>
      </div>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action">
        <van-button class="action-item-main" color="#108EE9" @click="handleSubmit">提交</van-button>
      </div>

      <van-popup v-model="dateShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          v-model="currentTime"
          type="date"
          @confirm="handleDateConfirm"
          @cancel="dateShow = false"
        />
      </van-popup>

      <van-popup
        v-model="departmentShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择部门</div>
        <a-scroll ref="filterScroller" v-if="departmentShow" class="filter-scroll">
          <van-radio-group v-model="departmentRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="departmentRadio = {...item}"
                v-for="(item, index) in departmentOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onDepartmentSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="typeShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择隐患类型</div>
        <a-scroll ref="filterScroller" v-if="typeShow" class="filter-scroll">
          <van-radio-group v-model="typeRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="typeRadio = {...item}"
                v-for="(item, index) in typeOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onTypeSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="levelShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择隐患级别</div>
        <a-scroll ref="filterScroller" v-if="levelShow" class="filter-scroll">
          <van-radio-group v-model="levelRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="levelRadio = {...item}"
                v-for="(item, index) in levelOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onLevelSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="areaShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择工区</div>
        <a-scroll ref="filterScroller" v-if="areaShow" class="filter-scroll">
          <van-radio-group v-model="areaRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="areaRadio = {...item}"
                v-for="(item, index) in areaOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onAreaSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="userShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择检查人</div>
        <a-scroll ref="filterScroller" v-if="userShow" class="filter-scroll">
          <van-checkbox-group v-model="userCheckbox">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="$refs.userCheckbox[index].toggle()"
                v-for="(item, index) in userOptions"
                :key="index"
              >
                <van-checkbox slot="right-icon" :name="item" ref="userCheckbox" />
              </van-cell>
            </van-cell-group>
          </van-checkbox-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onUserSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="professionShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择所属专业</div>
        <a-scroll ref="filterScroller" v-if="professionShow" class="filter-scroll">
          <van-radio-group v-model="professionRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="professionRadio = {...item}"
                v-for="(item, index) in professionOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onProfessionSelect">确定</van-button>
        </div>
      </van-popup>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import AScroll from '../../components/Scroll'
import navigate from '../../utils/navigate.js'
import AutoImage from '../../components/AutoImage'
import db from '../../db'
import io from '../../api'
import moment from 'moment'
import native from '../../utils/native.js'
import { uploadImage } from '../../utils/upload.js'

export default {
  components: {
    Page,
    AScroll,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,

    typeOptions: JSON.parse(localStorage.getItem('problem_category')),
    levelOptions: [
      {id: 'commonly', name: '一般'},
      {id: 'great', name: '重大'}
    ],
    departmentOptions: [],
    userOptions: [],
    areaOptions: [],
    professionOptions: JSON.parse(localStorage.getItem('problem_profession')),
    imageFilter: null,

    currentTime: new Date(),
    dateShow: false,

    departmentShow: false,
    departmentRadio: undefined,

    departmentType: 1,

    userShow: false,
    userCheckbox: [],

    typeShow: false,
    typeRadio: undefined,

    levelShow: false,
    levelRadio: undefined,

    areaShow: false,
    areaRadio: undefined,

    professionShow: false,
    professionRadio: null,

    form: {
      exameDate: '',
      exameDepartment: '',
      exameUser: [],
      troubleType: '',
      troubleContent: '',
      troubleSuggest: '',
      dutyDepartment: '',
      dutyArea: '',
      imageList: [],
      deviceCategory: null,
      profession: null,
      address: '',
      troubleLevel: '',
      tempSolution: '',
      finalSolution: '',
      remark: ''
    }
  }),
  methods: {
    onReady(header, main) {
      this.header = header

      this.headerHeightVW = ((this.header.h + 1) * 100) / this.header.w

      io.getTroubleDepartment().then(data => {
        this.departmentOptions = data
      })

    },

    onShow() {
      if (localStorage.getItem('category_select')) {
        const category = JSON.parse(localStorage.getItem('category_select'))

        this.form.deviceCategory = category[category.length - 1]
      }
    },

    onClickLeft() {
      navigate.back()
    },

    handleSubmit () {
      if (
        !this.form.exameDate || 
        !this.form.exameDepartment ||
        !this.form.exameUser || 
        !this.form.troubleType ||
        !this.form.troubleContent || 
        !this.form.dutyDepartment
      ) {
        this.$toast.fail('请填写所有必填项,可填写无')
      } else {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          loadingType: 'spinner',
          message: '正在提交...'
        })

        uploadImage(this.form.imageList).then(s => {
          this.form.imageList = s

          io.createTrouble({
            date: this.form.exameDate,
            department: this.form.exameDepartment ? this.form.exameDepartment.id : '',
            user: this.form.exameUser.map(item => item.id).join(','),
            profession: this.form.profession ? this.form.profession.id : '',
            deviceCategory: this.form.deviceCategory ? this.form.deviceCategory.id :'',
            address: this.form.address,
            content: this.form.troubleContent,
            harm: this.form.troubleSuggest,
            tempSolution: this.form.tempSolution,
            finalSolution: this.form.finalSolution,
            level: this.form.level ? this.form.level.id :'',
            type: this.form.type ? this.form.type.id : '',
            workarea: this.form.dutyArea ? this.form.dutyArea.id : '',
            myarea: this.form.dutyDepartment ? this.form.dutyDepartment.id :'',
            remark: this.form.remark,
            imgList: this.form.imageList
          })
            .then(data => {
              navigate.back()
            })
            .catch(msg => {
              this.$toast(msg)
            })
        })
      }
    },

    handleSelectExameDate () {
      this.dateShow = true
    },

    handleDateConfirm (time) {
      this.form.exameDate = moment(time).format('YYYY-MM-DD')
      this.dateShow = false
    },

    handleSelectDepartment () {
      this.departmentShow = true
      this.departmentRadio = {...this.form.exameDepartment}
      this.departmentType = 1
    },

    handleSelectDeviceCategory () {
      navigate.open('deviceCategory', {profession: this.form.profession ? this.form.profession.id : null})
    },

    handleSelectDepartment2 () {
      this.departmentShow = true
      this.departmentRadio = {...this.form.dutyDepartment}
      this.departmentType = 2
    },

    onDepartmentSelect () {
      if (this.departmentType == 1) {
        this.form.exameDepartment = {...this.departmentRadio}
      } else {
        this.form.dutyDepartment = {...this.departmentRadio}
      }
      this.departmentShow = false
    },

    handleSelectUser () {
      this.userShow = true
      this.userCheckbox = [...this.form.exameUser]
    },

    onUserSelect () {
      this.form.exameUser = [...this.userCheckbox]
      this.userShow = false
    },

    handleSelectProfession() {
      this.professionShow = true
      this.professionRadio = { ...this.form.profession }
    },

    onProfessionSelect() {
      this.form.profession = { ...this.professionRadio }
      this.professionShow = false
    },

    handleSelectType () {
      this.typeShow = true
      this.typeRadio = {...this.form.troubleType}
    },

    onTypeSelect () {
      this.form.troubleType = {...this.typeRadio}
      this.typeShow = false
    },

    handleSelectLevel () {
      this.levelShow = true
      this.levelRadio = {...this.form.troubleLevel}
    },

    onLevelSelect () {
      this.form.troubleLevel = {...this.levelRadio}
      this.levelShow = false
    },

    handleSelectArea () {
      this.areaShow = true
      this.areaRadio = {...this.form.dutyArea}
    },

    onAreaSelect () {
      this.form.dutyArea = {...this.areaRadio}
      this.areaShow = false
    },

    handlePhoto1() {
      api.getPicture(
        {
          sourceType: 'camera',
          destinationType: 'url',
          preview: true,
          quality: 50,
          mediaValue: 'pic'
        },
        (ret, err) => {
          // alert(JSON.stringify(ret))

          this.form.imageList.push({
            url: ret.data
          })
        }
      )
    },

    handleImageRemove(index) {
      this.form.imageList.splice(index, 1)
    },

    handlePlayImages(index) {
      localStorage.setItem('photos', JSON.stringify(this.form.imageList))
      navigate.open('photoViewer', { index: index })
    },

  },
  watch: {
    'form.exameDepartment': {
      deep: false,
      handler (val) {
        if (val.id) {
          io.searchOrganization({
            department: val.id
          }).then(data => {
            this.form.exameUser =  []
            this.userOptions = data.users
          })
        }
      }
    },

    'form.dutyDepartment': function (val) {
      if (val.id) {
        io.getTroubleDepartment({parent: val.id}).then(data => {
          this.form.dutyArea = null

          this.areaOptions = data
        })
      }
    }
  }
}
</script>

<style lang="scss"></style>
